:root {
  --darkBlue: hsl(233, 26%, 24%);
  --limeGreen: hsl(136, 65%, 51%);
  --rightCyan: hsl(192, 70%, 51%);
  --grayishBlue: hsl(233, 8%, 62%);
  --lightGrayishBlue: hsl(220, 16%, 96%);
  --veryLightGray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-inter: "Public Sans", sans-serif;
}

/*
  * use break points for mobile first approach
*/
/*
  * use break points for desktop first approach
*/
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--lightGrayishBlue);
}

ul,
li {
  list-style: none;
}

.btn-cta {
  font-weight: 700;
  border: none;
  border-radius: 20px;
  color: var(--white);
  text-transform: capitalize;
  background: linear-gradient(to right, var(--limeGreen), var(--rightCyan));
  opacity: 1;
  cursor: pointer;
}
.btn-cta:hover {
  opacity: 0.8;
  transition: all 100ms ease-out;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--darkBlue);
  text-decoration: none;
}

.navbar {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 4.0625rem;
  margin: 0 auto;
  width: 100%;
  padding: 0 1.5625rem;
  z-index: 11;
}
@media screen and (min-width: 40rem) {
  .navbar {
    padding: 0 3.125rem;
  }
}
@media screen and (min-width: 64rem) {
  .navbar {
    padding: 0 6.5625rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .navbar {
    padding: 0 10.3125rem;
  }
}
.navbar__logo {
  width: 8.75rem;
  z-index: 11;
}
.navbar__logo img {
  display: block;
  width: 100%;
}
.navbar__menu {
  width: 1.5625rem;
  height: 1.375rem;
  padding-top: 0.0625rem;
  z-index: 11;
}
.navbar__menu span {
  display: block;
  width: 100%;
  height: 0.1875rem;
  background: var(--darkBlue);
}
.navbar__menu span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
@media screen and (min-width: 64rem) {
  .navbar__menu {
    display: none;
  }
}
.navbar .show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
  transition: all ease-in-out 300ms;
}
.navbar__links_container {
  position: absolute;
  width: 100%;
  height: 90vh;
  top: 100%;
  left: 0;
  transform: translateY(-10%);
  font-size: 1rem;
  background: linear-gradient(var(--darkBlue), rgba(0, 0, 0, 0.3));
  visibility: hidden;
  opacity: 0.1;
  transition: all ease-in-out 100ms;
  z-index: -1;
}
@media screen and (min-width: 64rem) {
  .navbar__links_container {
    width: auto;
    position: static;
    visibility: visible;
    opacity: 1;
    height: auto;
    transform: translateY(0);
    background: transparent;
  }
}
.navbar__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 0;
  margin: 1.5625rem;
  border-radius: 0.3125rem;
  background-color: #fff;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
.navbar__links .link {
  position: relative;
  margin: 0.9375rem auto;
  text-transform: capitalize;
}
.navbar__links .link > a {
  font-weight: 500;
  color: var(--grayishBlue);
}
.navbar__links .link:hover:after {
  position: absolute;
  content: "";
  margin-top: 1.125rem;
  top: 100%;
  left: 0;
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(to right, var(--limeGreen), var(--rightCyan));
  animation: showUnderline ease-in 100ms;
}
@media screen and (min-width: 64rem) {
  .navbar__links .link {
    margin-right: 1.875rem;
  }
}
@media screen and (min-width: 64rem) {
  .navbar__links {
    background-color: transparent;
    flex-direction: row;
    justify-content: space-around;
  }
}
.navbar .btn-cta {
  display: none;
  padding: 0.625rem 1.875rem;
  font-size: 0.875rem;
}
@media screen and (min-width: 64rem) {
  .navbar .btn-cta {
    display: block;
  }
}

@keyframes showUnderline {
  0% {
    transform: scalex(0);
  }
  100% {
    transform: scalex(1);
  }
}